<template>
  <div id="app">
    <h1>{{ $t("title") }}</h1>

    <!-- Language Switcher -->
    <div class="language-switcher">
      <button @click="changeLanguage('en')">English</button>
      <button @click="changeLanguage('ru')">Русский</button>
    </div>

    <h2 v-if="!weather">{{ $t("loading") }}</h2>
    <div v-else>
      <label>
        <input
          type="radio"
          value="metric"
          v-model="units"
          @change="updateWeather"
        />
        {{ $t("celsius") }}
      </label>
      <label>
        <input
          type="radio"
          value="imperial"
          v-model="units"
          @change="updateWeather"
        />
        {{ $t("fahrenheit") }}
      </label>

      <label for="city">{{ $t("select_city") }}</label>
      <select id="city" v-model="city" @change="updateWeather">
        <option v-for="city in cities" :key="city" :value="city">{{ city }}</option>
      </select>

      <div class="favorites-actions">
        <button @click="addToFavorites">{{ $t("add_to_favorites") }}</button>
      </div>

      <h3>{{ $t("favorites") }}</h3>
      <ul class="favorites-list">
        <li 
          v-for="fav in favorites" 
          :key="fav" 
          @click="selectFavorite(fav)"
          class="favorite-item"
        >
          {{ fav }}
          <button 
            @click.stop="removeFromFavorites(fav)" 
            class="remove-favorite"
          >
            ×
          </button>
        </li>
      </ul>

      <p>{{ $t("temperature") }}: {{ weather.main.temp }} {{ unitSymbol }}</p>
      <p>{{ $t("description") }}: {{ weather.weather[0].description }}</p>
      <p>{{ $t("humidity") }}: {{ weather.main.humidity }}%</p>
      <p>{{ $t("pressure") }}: {{ weather.main.pressure }} hPa</p>
      <p>{{ $t("wind") }}: {{ weather.wind.speed }} {{ $t("wind_unit") }}, {{ weather.wind.deg }}°</p>
      <img 
        :src="`http://openweathermap.org/img/wn/${weather.weather[0].icon}@2x.png`" 
        :alt="weather.weather[0].description" 
      />

      <WeatherForecast v-if="forecast" :forecast="forecast" />
    </div>
  </div>
</template>


<script>
import WeatherForecast from './components/WeatherForecast.vue';

export default {
  name: "App",
  components: {
    WeatherForecast,
  },
  data() {
    return {
      weather: null,
      forecast: null,
      city: "Нижний Тагил",
      cities: [
        "Нижний Тагил",
        "Москва",
        "Санкт-Петербург",
        'Екатеринбург',
        'Новосибирск',
        'Казань',
        'Челябинск',
        'Уфа',
        'Самара',
        'Омск',
        'Ростов-на-Дону',
        'Красноярск',
        'Воронеж',
        'Пермь',
        'Волгоград',
        'Краснодар',
        'Тюмень',
        'Саратов',
        'Ижевск',
        'Тольятти',
        'Барнаул',
        'Ульяновск',
        'Иркутск',
        'Хабаровск',
        'Ярославль',
        'Владивосток',
        'Махачкала',
        'Томск',
        'Оренбург',
        'Кемерово',
        'Рязань',
        'Астрахань',
        'Набережные Челны',
        'Пенза',
        'Липецк',
        'Тула',
        'Киров',
        'Чебоксары',
        'Курск',
        'Брянск',
        'Иваново',
        'Магнитогорск',
        'Тверь',
        'Калининград',
        'Сочи',
        'Белгород',
        'Ставрополь',
        'Нижний Новгород'
      ],
      units: "metric", 
      favorites: JSON.parse(localStorage.getItem("favorites")) || [], 
    };
  },
  computed: {
    unitSymbol() {
      return this.units === "metric" ? "°C" : "°F";
    },
  },
  mounted() {
    this.updateWeather();
  },
  methods: {
    updateWeather() {
      this.fetchWeather();
      this.fetchForecast();
    },
    fetchWeather() {
      const apiKey = "6019323cca29a654a91661580504dbbf";
      fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=${this.city}&appid=${apiKey}&units=${this.units}&lang=${this.$i18n.locale}`
      )
        .then((resp) => {
          if (!resp.ok) throw new Error(`Ошибка: ${resp.status}`);
          return resp.json();
        })
        .then((json) => {
          this.weather = json;
        })
        .catch((error) => {
          console.error("Ошибка получения данных о погоде:", error);
          alert(this.$t("error_weather"));
        });
    },
    fetchForecast() {
      const apiKey = "6019323cca29a654a91661580504dbbf";
      fetch(
        `https://api.openweathermap.org/data/2.5/forecast?q=${this.city}&appid=${apiKey}&units=${this.units}&lang=${this.$i18n.locale}`
      )
        .then((resp) => {
          if (!resp.ok) throw new Error(`Ошибка: ${resp.status}`);
          return resp.json();
        })
        .then((json) => {
          const dailyForecast = [];
          const addedDates = new Set();
          json.list.forEach((item) => {
            const date = new Date(item.dt * 1000).toLocaleDateString("ru-RU");
            if (!addedDates.has(date)) {
              dailyForecast.push(item);
              addedDates.add(date);
            }
          });
          this.forecast = dailyForecast;
        })
        .catch((error) => {
          console.error("Ошибка получения данных прогноза:", error);
          alert(this.$t("error_forecast"));
        });
    },
    addToFavorites() {
      if (!this.favorites.includes(this.city)) {
        this.favorites.push(this.city);
        localStorage.setItem("favorites", JSON.stringify(this.favorites));
      }
    },
    removeFromFavorites(city) {
      this.favorites = this.favorites.filter((fav) => fav !== city);
      localStorage.setItem("favorites", JSON.stringify(this.favorites));
    },
    selectFavorite(city) {
      this.city = city;
      this.updateWeather();
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.updateWeather();
    },
  },
};
</script>

