<template>
  <div class="forecast">
    <h2>Прогноз погоды на ближайшие дни</h2>
    <div class="forecast-row">
      <div class="forecast-day" v-for="(day, index) in forecast" :key="index">
        <p><strong>{{ formatDate(day.dt) }}</strong></p>
        <img
          :src="`http://openweathermap.org/img/wn/${day.weather[0].icon}@2x.png`"
          :alt="day.weather[0].description"
        />
        <p>Темп: {{ day.main.temp }} °C</p>
        <p>{{ day.weather[0].description }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "WeatherForecast",
  props: {
    forecast: {
      type: Array,
      required: true
    }
  },
  methods: {
    formatDate(timestamp) {
      const options = { weekday: 'short', day: 'numeric', month: 'short' };
      return new Date(timestamp * 1000).toLocaleDateString('ru-RU', options);
    }
  }
};
</script>

<style scoped>
.forecast {
  margin-top: 20px;
  text-align: center;
}

.forecast-row {
  display: flex;
  justify-content: center;
  gap: 15px;
  overflow-x: auto;
  padding: 10px 0;
}

.forecast-day {
  flex: 0 0 auto;
  width: 120px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  padding: 10px;
  text-align: center;
}

.forecast-day img {
  max-width: 60%;
  margin: 5px 0;
}

.forecast-day p {
  margin: 5px 0;
  font-size: 14px;
}
</style>
